	module.exports = {"data":{"content":{"content":"<h2 id=前言>前言</h2>\n<p>良好的代碼規範，能夠提高代碼的可閱性，使得項目維護更友好。除了自己設定一個規範外，我們也可以借鑒一下現在流行的代碼規範，讓我們成長之後，再去看細節。</p>\n<p><a data-fancybox=\"gallery\" href=\"https://i.imgur.com/DPltr6s.png\"><img class=\"lozad d-block mx-auto\" data-src=https://i.imgur.com/DPltr6s.png title=\"Code Quality: Good Code vs Bad Code\" /></a></p>\n<h2 id=Airbnb>Airbnb</h2>\n<p>Airbnb 是其中一個最流行的 JavaScript 代碼規範，它差不多包含所有 JavaScript 的角度。它也是我的個人項目所使用的代碼規範。如果你的項目是基於 React 的，那麼你可以選擇安裝 <a href=\"https://www.npmjs.com/package/eslint-config-airbnb\">eslint-config-airbnb</a>，或者你可以選擇最基本的 <a href=\"https://www.npmjs.com/package/eslint-config-airbnb-base\">eslint-config-airbnb-base</a>。</p>\n<p><a href=\"https://www.npmjs.com/package/eslint-config-airbnb\">eslint-config-airbnb</a> 包含 ECMAScript 6 + 以及 React 的 ESLint 代碼規範。在安裝 <code>eslint-config-airbnb</code> 的時候，它會一同安裝 <code>eslint</code>, <code>eslint-plugin-import</code>, <code>eslint-plugin-react</code>, and <code>eslint-plugin-jsx-a11y</code>。如果你的項目不是 React 的話，那麼你可以選擇<code>eslint-config-airbnb-base</code>。</p>\n<pre><code><pre><code class=\"hljs javascript\">npm install eslint-config-airbnb\n</code></pre></code></pre>\n<p><a href=\"https://www.npmjs.com/package/eslint-config-airbnb-base\">eslint-config-airbnb-base</a> 包含ECMAScript 6 + 的 ESLint 代碼規範。安裝它的時候，它會需要 <code>eslint</code> 和 <code>eslint-plugin-import</code>。</p>\n<pre><code><pre><code class=\"hljs javascript\">npm install eslint-config-airbnb-base\n</code></pre></code></pre>\n<p>然後在你的 <code>.eslintrc</code> 加入 <code>&quot;extends&quot;: &quot;airbnb-base&quot;</code> 就可以了。</p>\n<p>Airbnb 的完整代碼規範可以在 <a href=\"https://github.com/airbnb/javascript\">airbnb/javascript</a> 上閱讀。</p>\n<h2 id=Standard>Standard</h2>\n<p>除了 Airbnb 的代碼規範之外，有很多高科技公司在用 Standard 的代碼規範，包括但不限於 NPM, Github, mongoDB, ZenDesk。</p>\n<p><a data-fancybox=\"gallery\" href=\"https://i.imgur.com/fw8cwnyh.jpg\"><img class=\"lozad d-block mx-auto\" data-src=https://i.imgur.com/fw8cwnyl.jpg title=\"Companies that use Standard\" /></a></p>\n<p>這裡是官網提到的一些 Standard 規範：</p>\n<ul>\n<li>兩個空白 － 當作縮排</li>\n<li>字串用單引號 － 除非要避免跳脫字元</li>\n<li>沒有不必要的變數</li>\n<li>不要加分號</li>\n<li>絕對不要用 ( 、 [ 當開頭</li>\n<li>這是不用分號 唯一 可能遇到的問題 － 那就自動幫你檢查吧！</li>\n<li>關鍵字後加空白 if (condition) { ... }</li>\n<li>函數名稱後加空白 function name (arg) { ... }</li>\n<li>統一用 === 取代 == － 但是 obj == null 可以用來檢查 null || undefined。</li>\n<li>一定要例外處理 node.js 中的 err 參數</li>\n<li>一定要對瀏覽器中的全域變數加上 window 前綴 － 除了 document 和 navigator 可以不用</li>\n<li>避免使用那些命名得很爛的全域變數，像是 open 、 length 、 event 和 name。</li>\n</ul>\n<p>你可以透過 <code>eslint-config-standard</code> 來自動校對，改善你的代碼。</p>\n<pre><code class=\"language-shell\"><pre><code class=\"hljs shell\">npm install eslint-config-standard\n</code></pre></code></pre>\n<p><a href=\"https://github.com/standard/standard/blob/master/docs/README-zhtw.md\">Standard 繁體中文文檔</a>\n<a href=\"https://github.com/standard/standard/blob/master/docs/README-zhcn.md\">Standard 簡體中文文檔</a></p>\n<h2 id=Idiomatic>Idiomatic</h2>\n<p>它的思想是無論誰改進項目的代碼，都應該像只有一個人寫代碼。</p>\n<p>你可以透過 eslint-config-idiomatic 來自動校對，改善你的代碼。</p>\n<pre><code><pre><code class=\"hljs javascript\">npm install eslint-config-idiomatic\n</code></pre></code></pre>\n<p><a href=\"https://github.com/rwaldron/idiomatic.js/\">Idiomatic - Github</a></p>\n<p>備註：</p>\n<p>use version ^3.0.0 for ESLint version ^3.x.x.\nuse version ^2.0.0 for ESLint version ^2.x.x.\nuse version ^1.0.0 for ESLint version ^1.x.x.</p>\n<p>在你的 .eslintrc 內輸入：</p>\n<pre><code><pre><code class=\"hljs javascript\">{\n  <span class=\"hljs-string\">\"extends\"</span>: <span class=\"hljs-string\">\"idiomatic\"</span>\n}\n</code></pre></code></pre>\n<h2 id=Google>Google</h2>\n<p>你都可以像 Google 工程師一樣編寫同樣風格的代碼。</p>\n<p>你可以透過 eslint-config-google 來自動校對，改善你的代碼。</p>\n<pre><code><pre><code class=\"hljs javascript\">npm install eslint eslint-config-google\n</code></pre></code></pre>\n<p>我們可以在 <a href=\"https://google.github.io/styleguide/jsguide.html\">Google - GitHub Pages</a> 上閱讀它的代碼規範。</p>\n<h2 id=jQuery>jQuery</h2>\n<p>如果你想要幫助 jQuery 改善它的代碼的話，你可以在 <a href=\"https://contribute.jquery.org/style-guide/js/\">jQuery 的官網</a> 上閱讀它的代碼規範。</p>\n<h2 id=總結>總結</h2>\n<p>自從看了《Clean Code》一書之後，我感覺寫代碼不只是一種手藝，還是藝術。可能你看過去感覺都是 JavaScript，但是讀著讀著一些開源項目，就會感受到代碼可以有多麼優雅，同時可以有多麼不雅。。。為了減少他人說 WTF 的機會，我們還是研究一下如何把自己的代碼變得更加優雅吧。</p>\n<p>如果你是第一次使用 JavaScript 代碼規範的話，那麼你可以選擇 Airbnb 作為你的代碼規範。Airbnb 的文檔可閱性是不錯的，而且把現有項目加上 eslint 也不是什麼難事。</p>\n<p>如果要仔細地說代碼規範的好處，重要性的話，我們可以在下一篇文章討論。</p>\n<h2 id=參考資料>參考資料</h2>\n<ol>\n<li><a href=\"http://www.cbs.dtu.dk/courses/27610/clean_code_index.html\">Clean Code 電子書</a></li>\n<li><a href=\"https://codeburst.io/5-javascript-style-guides-including-airbnb-github-google-88cbc6b2b7aa\">5 JavaScript Style Guides — Including AirBnB, GitHub, &amp; Google</a></li>\n</ol>\n","title":"你可能不知道的 JavaScript 代碼規範","createdDate":"2017-11-08T20:00+08:00","headerImgur":"KMksHxZ.png","id":"3h9VEKrlaEqAcAAMsikqI4","toc":["前言","Airbnb","Standard","Idiomatic","Google","jQuery","總結","參考資料"],"tags":"JavaScript, eslint"},"latestPosts":{"totalCount":60,"edges":[{"node":{"title":"【網易考拉校招】部門總監面對面-直播答疑邀約（附網易內推碼B222E54）","url":"netease-kaola-live","createdDate":"2018-08-01T20:00+08:00"}},{"node":{"title":"Uncaught ReferenceError: regeneratorRuntime is not defined 解決方法","url":"regenerator-runtime-is-not-defined","createdDate":"2018-07-29T20:00+08:00"}},{"node":{"title":"關於作者","url":"about-calpa-liu","createdDate":"2018-05-01T20:00+08:00"}},{"node":{"title":"【方法】在直接複製貼上內容的時候，可能已經不知不覺注入了一段不可視的「空白」","url":"insert-zero-width-characters","createdDate":"2018-04-06T23:00+08:00"}},{"node":{"title":"知乎無限 live 數據流網站","url":"zhihu-unlimited-lives-website","createdDate":"2018-03-28T20:49+08:00"}},{"node":{"title":"Gatsbyjs async/await 更新報錯解決方法","url":"gatsybjs-async-await-upgrade-bug-solution","createdDate":"2018-03-21T20:00+08:00"}}]}},"pathContext":{"id":"3h9VEKrlaEqAcAAMsikqI4"}}

